import rssData from './data/rssData.json';
import webContent from './data/webContent.json';
import { mergeDeepLeft } from 'ramda';
import { CategoryGroup, RSSNews } from './type/app';
import { Fragment } from 'react';
import { Section } from './Section';

type NewsGroupByCategory = [string, CategoryGroup];

const currentYear = new Date().getFullYear();

function getFormattedDate() {
  const koreanLocale = 'ko-KR';

  const today = new Date();
  const formattedDate = today.toLocaleDateString(koreanLocale);

  return formattedDate;
}

function sortCategories(arr: NewsGroupByCategory[], priorityOrder: string[]) {
  // priorityOrder     
  function getPriority(category: string) {
    if (category === 'etc') {
      return Infinity; // 'etc'        
    }
    let index = priorityOrder.indexOf(category);
    return index === -1 ? priorityOrder.length : index;
  }

  //      
  return arr.sort((a, b) => {
    let priorityA = getPriority(a[0]);
    let priorityB = getPriority(b[0]);
    return priorityA - priorityB;
  });
}

function App() {
  const mergedObj = mergeDeepLeft(rssData, webContent) as RSSNews;
  const newsEntries = sortCategories(Object.entries(mergedObj), ['news']);

  return (
    <div>
      <div className="head">
        <header>
          <h1 className="title">TOMINEWS</h1>
        </header>
        <div className="subhead">{getFormattedDate()}</div>
      </div>
      {newsEntries && newsEntries.length > 0 ? (
        <div className="content">
          {newsEntries?.map(([category, columns]) => {
            return (
              <Fragment key={category}>
                {Object.entries(columns)?.map(([sectionTitle, articles]) => {
                  return (
                    <Section
                      articles={articles}
                      category={category}
                      sectionTitle={sectionTitle}
                    />
                  );
                })}
              </Fragment>
            );
          })}
        </div>
      ) : (
        <div>No news for today</div>
      )}
      <footer className="footer">
        <div className="copyright"> {currentYear} Plainbear.</div>
        <div className="request">
            {' '}
          <a href="mailto:plainbear.adm@gmail.com">plainbear.adm@gmail.com</a>
          .
        </div>
      </footer>
    </div>
  );
}

export default App;
