const parseFunctions = {
  Quasarzone: $ => {
    const content = $(
      '.mid-content-area .tab-con-utit:first-child .right-box ul',
    );
    const output = [];
    content.children('li').each(function (i, elm) {
      const $elm = $(elm).children('a');
      output.push({
        link: 'https://quasarzone.com' + $elm.attr('href'),
        title: $elm.text().trim(),
      });
    });
    return output;
  },
  'Naver Football': $ => {
    const content = $('#_ranking_news_list_0');
    const output = [];
    content.children('li').each(function (i, elm) {
      const $elm = $(elm).children('a');
      output.push({
        link: $elm.attr('href'),
        title: $elm.text().trim(),
      });
    });
    return output;
  },
  Mule: $ => {
    const content = $('.browse-wrapper+.board-list-wrapper tbody');
    const output = [];
    content.children('tr').each(function (i, elm) {
      const $elm = $(elm).find('.title a');
      $elm.find('span, div').remove();
      const link = $elm.attr('href');
      if (link) {
        output.push({
          link: `https://www.mule.co.kr${link}`,
          title: $elm.text().trim(),
        });
      }
    });
    return output;
  },
  'Kinolights Ranking': $ => {
    const content = $('.ranking-list-wrap .ranking-container');
    const output = [];
    content.children('li').each(function (i, elm) {
      const $elm = $(elm).find('a');
      const link = $elm.attr('href');
      const title = $elm.find('.info__title').text().trim();
      output.push({
        link: `https://m.kinolights.com${link}`,
        title: `${i + 1}. ${title}`,
      });
    });
    return output;
  },
  AAGAG: $ => {
    const content = $('.aalist .class_title');
    const targetTitle = content.filter(function () {
      return $(this).text().trim().startsWith(' ');
    });
    if (!targetTitle) return [];
    const parentElement = targetTitle.parent();
    const output = [];
    parentElement.children('a').each(function (i, elm) {
      const $elm = $(elm);
      const link = $elm.attr('href');
      const title = $elm.find('.title').text().trim();
      output.push({
        link: `https://aagag.com${link}`,
        title,
      });
    });
    return output;
  },
};

