import { Article } from './type/app';
import { webContent as webContentSource } from './config/webContent';
import rssFeeds from './config/rssFeeds';
import { useState } from 'react';

interface SectionProps {
  articles: Article[];
  category: string;
  sectionTitle: string;
}

const INITIAL_DISPLAY_COUNT = 10;

function getLinkOfSource(title: string) {
  const sourceList = rssFeeds.concat(webContentSource);
  return sourceList.find(source => source.name === title)?.url;
}

export function Section({ articles, category, sectionTitle }: SectionProps) {
  const [showAll, setShowAll] = useState(false);

  const handleToggle = () => {
    setShowAll(!showAll);
  };

  const displayedArticles = showAll
    ? articles
    : articles.slice(0, INITIAL_DISPLAY_COUNT);
  const showToggleButton = articles.length > INITIAL_DISPLAY_COUNT;

  return (
    <section key={sectionTitle} className={`column ${category}`}>
      <div className="head">
        <div className="headline">
          {/* <a
            href={getLinkOfSource(sectionTitle)}
            target="_blank"
            rel="noreferrer"
          > */}
          <span className="text">{sectionTitle}</span>
          {/* TODO: rss raw data  URL   */}
          {/* <span className="see-more"></span> */}
          {/* </a> */}
        </div>
      </div>
      {displayedArticles.map(({ title, link }) => (
        <a
          href={link}
          rel="noreferrer"
          key={title}
          className="article"
          target="_blank"
          title={title}
        >
          <h3 className="article-title">{title}</h3>
        </a>
      ))}
      {showToggleButton && (
        <button className="toggle-button" onClick={handleToggle}>
          {showAll ? '' : ''}
        </button>
      )}
    </section>
  );
}
